import { ComponentChildren } from 'preact';
import { Layer } from '../../UNSAFE_Layer';
import { Floating, Placement } from '../../UNSAFE_Floating';
import { useRef, useState } from 'preact/hooks';
import { styles } from './themes/useDatatip.css';
import { useId } from '../UNSAFE_useId';
import { Property } from 'csstype';
import { DATATIP_OFFSET } from '#utils/UNSAFE_visUtils/datatipUtils';
import { useVisHover } from '#hooks/PRIVATE_useVisHover';
import { mergeProps } from '#utils/UNSAFE_mergeProps';
export type { Dimension } from '../../utils/UNSAFE_visTypes/common';

type DatatipProps = {
  /**
   * The datatip content.
   */
  content?: ComponentChildren;

  /**
   * Whether the datatip is anchored to pointer or element.
   *
   */
  anchor: 'element' | 'pointer';

  /**
   * The offset from the anchor element or pointer.
   */
  offset?: {
    mainAxis?: number;
    crossAxis?: number;
  };

  /**
   * The border color of the datatip.
   */
  borderColor?: Property.BorderColor;

  /**
   * The placement of datatip from anchor point.
   */
  placement?: Placement;

  /**
   * The touch response value.
   */
  touchResponse: 'touchStart' | 'touchHold';
};

/**
 * The hook to support custom datatip in visualization components.
 */
export function useDatatip({
  content,
  placement = 'top-end-corner',
  offset,
  borderColor,
  anchor,
  touchResponse
}: DatatipProps) {
  const uniqueIdRef = useRef<string>(useId());
  const [coords, setCoords] = useState({ x: 0, y: 0 });
  const offsetValue = offset || { mainAxis: DATATIP_OFFSET, crossAxis: -1 * DATATIP_OFFSET };
  const [isVisible, setIsVisible] = useState(false);

  const targetRef = useRef<HTMLElement | null>(null);
  const toggleTimeoutRef = useRef<ReturnType<typeof setTimeout>>();

  const onHover = (event: PointerEvent) => {
    setCoords({ x: event.clientX, y: event.clientY });
    setIsVisible(true);
  };

  const onHoverEnter = (event: PointerEvent, currentTarget: HTMLElement) => {
    if (
      event.pointerType === 'touch' ||
      event.eventPhase === Event.AT_TARGET ||
      event.eventPhase === Event.BUBBLING_PHASE
    ) {
      targetRef.current = currentTarget;
      setIsVisible(true);
    }
  };

  const onHoverLeave = () => {
    toggleTimeoutRef.current = setTimeout(() => {
      setIsVisible(false);
    }, 20);
  };

  const hoverHandlers = useVisHover(onHover, onHoverEnter, onHoverLeave, touchResponse);

  const onFocus = (event: FocusEvent) => {
    if (event.eventPhase === Event.AT_TARGET || event.eventPhase === Event.BUBBLING_PHASE) {
      targetRef.current = event.target as HTMLElement;
      setIsVisible(true);
    }
  };

  const onBlur = () => {
    setIsVisible(false);
  };

  const onContentPointerEnter = () => {
    clearTimeout(toggleTimeoutRef.current);
  };

  const onKeyUp = (event: KeyboardEvent) => {
    if (event.code === 'Escape') {
      event.preventDefault();
      setIsVisible(false);
    } else if (
      event.code === 'ArrowUp' ||
      event.code === 'ArrowDown' ||
      event.code === 'ArrowRight' ||
      event.code === 'ArrowLeft' ||
      event.code === 'Tab'
    ) {
      setIsVisible(true);
    }
  };

  const anchorRef = anchor === 'element' ? targetRef : { current: coords };
  const datatipContent =
    isVisible && content != null ? (
      <Layer>
        <Floating
          anchorRef={anchorRef}
          placement={placement}
          offsetValue={offsetValue}
          flipOptions={{ crossAxis: false, mainAxis: false }}
          shiftOptions={{ crossAxis: true, mainAxis: true }}>
          <div
            role="tooltip"
            id={uniqueIdRef.current}
            className={styles.visDatatipStyle}
            style={{ borderColor }}
            onPointerEnter={onContentPointerEnter}
            onPointerLeave={onHoverLeave}>
            {content}
          </div>
        </Floating>
      </Layer>
    ) : null;

  return {
    datatipContent,
    datatipProps: {
      'aria-describedby': datatipContent !== null ? uniqueIdRef.current : undefined,
      ...mergeProps(hoverHandlers, {
        onKeyUp,
        onFocus,
        onBlur
      })
    }
  };
}
